@font-face {
  font-family: "Roboto";
  src: url(./resources/Roboto-Regular.ttf), format("ttf");
  font-weight: normal;
}
@font-face {
  font-family: "Roboto";
  src: url(./resources/Roboto-Bold.ttf), format("ttf");
  font-weight: bold;
}
@font-face {
  font-family: "Ubuntu Mono";
  src: url(./resources/UbuntuMono-Regular.ttf), format("ttf");
  font-weight: normal;
}
@font-face {
  font-family: "Ubuntu Mono";
  src: url(./resources/UbuntuMono-Bold.ttf), format("ttf");
  font-weight: bold;
}
@font-face {
  font-family: "Ubuntu Mono";
  src: url(./resources/UbuntuMono-Italic.ttf), format("ttf");
  font-style: italic;
}
@font-face {
  font-family: "Ubuntu Mono";
  src: url(./resources/UbuntuMono-BoldItalic.ttf), format("ttf");
  font-weight: bold;
  font-style: italic;
}
@font-face {
  font-family: "OpenSans";
  src: url(./resources/OpenSans-Regular.ttf), format("ttf");
  font-weight: normal;
}

small {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: smaller;
}

#dashboard {
  text-align: center;
  margin: 0em 2% 0em 2%;
}

.message {
  text-align: center;
}

.m1 {
  margin: 1em;
}

code {
  color: red;
  font-family: Consolas;
}

#map-container {
  /* display: block; */
  /* text-align: right; */
  position: relative;
  width: 100%;
  height: 100%
}

#map-container #map-ui {
  position: absolute;
  top: 0;
  left: 0;
  /*
  right: 0;
  bottom: 0; */
  /* z-index: 2; */
}

#map-ui .loading-label {
  color: white;
  font-family: Roboto;
  font-size: medium;
}

#map-background {
  /* display: inline-block; */
  position: absolute;
  z-index: 0;

}

#map-canvas {
  /* display: inline-block; */
  position: relative;
  /* z-index: 1; */
}

/* canvas {
  display: inline;
} */

.link-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: inline;
  color: skyblue;
  margin: 0;
  padding: 0;
}
.link-button:hover {
  text-decoration: underline;
}

.waypoint-count {
  padding: 0.5em;
}

.waypointList {
  display: none;
}

.waypointList.responsive {
  display: block;
  clear: both;
  padding: 0;
  height: 160px;
}

.p-1 {
  padding: 1em
}

.pv-half {
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}

.hover-tag:hover {
  background-color: lightgray;
  cursor: help;
}


.tradegoods-table th {
  font-size: 9pt;
}

td.ant-table-cell {
  padding: 0px;
}


.tradegoods-table td {
  font-size: 8pt;
  padding: 0px;
  line-height: 5px;
}

.tradegoods-table .ant-btn {
  font-size: 8pt;
}

.bold {
  font-weight: bold;
}

.table-row td {
  padding: 1px !important;
}

.ant-statistic-title {
  font-size: 9pt !important;
}

.clickable-tag:hover {
  cursor: pointer;
  background-color: lightgray;
}

.table-popover {
  font-size: 8pt;
}
